<template>
  <div class="article">
    <!-- 头部 -->
    <Header></Header>
    <!-- /头部 -->
    <!-- 体部 -->
    <div class="content">
      <div class="wrapper">
        <div class="article_content" v-if="article">
          <div class="title">{{article.title}}</div>
          <div class="info">
            分类：{{article.category?article.category.name:'无'}}
            作者：{{article.baseUser?article.baseUser.realname:'匿名'}}
            发布时间：{{article.publishTime | fmtDate}}
            阅读次数：{{article.readTimes}}
          </div>
          <div class="cc" v-html="article.content"> </div>
        </div>
      </div>
    </div>
    <!-- /体部 -->
    <!-- 底部 -->
    <Footer></Footer>
    <!-- /底部 -->
  </div>
</template>

<script>
import Header from './components/Head'
import Footer from './components/Foot'
import { get } from '../utils/request'
export default {
  components:{
    Header,Footer
  },
  data(){
    return {
      id:0,
      article:null
    }
  },
  created(){
    this.id = this.$route.query.id;
    this.loadArticle();
  },
  methods:{
    loadArticle(){
      let url = '/index/article/findById'
      get(url,{id:this.id}).then(resp => {
        this.article = resp.data;
      })
    }
  }
  
}
</script>

<style lang="scss" scoped>
// 变量
$color_primary:#028b39;
.article {
  .content {
    background-color: #ecf6f2;
    padding: 1em 0;

    .article_content {
      background-color: #ffffff;
      min-height: 400px;
      border-radius: 5px;
      .title,.info {
        text-align: center;
      }
      .title {
        font-size:18px;
        font-weight:bold;
        color: $color_primary;
        line-height: 2em;
      }
      .info {
        padding:0 0 1em;
        width: 80%;
        margin: 0 auto;
        border-bottom: 1px solid #ededed;
        margin-bottom: .5em;
      }
      .cc {
        padding: 0 1em;
      }
    }
  }
}
</style>